@import url('https://fonts.googleapis.com/css?family=Candal|Lora');

* {
	box-sizing: border-box;
}

html,
body {
	height: 100%;
	padding: 0;
	margin: 0;
	background: #f6f6f6;
	font-family: "Lora", serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Candal", serif;
	color: #5c5b5b;
	margin: 5px;
}

a {
	text-decoration: none;
	color: inherit;
}

.clearfix::after {
	content: "";
	display: block;
	clear: both;
}

.btn {
	padding: 0.5rem 1rem;
	background: #006669;
	color: white;
	border: 1px solid transparent;
	border-radius: 0.25rem;
	font-size: 1.08em;
	font-family: "Lora", serif;
}

.btn-big {
	padding: 0.7rem 1.3rem;
	line-height: 1.3rem;
}

.btn:hover {
	color: white !important;
	background: #005255;
}

.text-input {
	padding: 0.7rem 1rem;
	display: block;
	width: 100%;
	border-radius: 5px;
	border: 1px solid #e0e0e0;
	outline: none;
	color: #444;
	line-height: 1.5rem;
	font-size: 1.2em;
	font-family: "Lora", serif;
}

.msg {
	width: 100%;
	margin: 5px auto;
	padding: 8px;
	border-radius: 5px;
	list-style: none;
}

.success {
	color: #3a6e3a;
	border: 1px solid #3a6e3a;
	background: #bcf5bc;
}

.error {
	color: #884b4b;
	border: 1px solid #884b4b;
	background: #f5bcbc;
}

/* NAVBAR */

/*.nav {
  display: none;
}

.nav.showing {
  display: block;
}

.nav li {
  margin: 10px 5px;
}

.nav a {
  padding: 10px 10px;
}*/

.dropdown-menu {
  display: none;
}

.dropdown-menu.showing {
  display: block;
}

header {
	background-color: pink;
	height: 66px;
}

header * {
	color: white;
}

header .logo {
	float: left;
	height: inherit;
	margin-left: 2em;
}

header .logo-text {
	margin: 9px;
	font-family: "Candal", serif;
}

header .logo-text span {
	color: #05f7ff;
}

header ul {
	float: right;
	margin: 0;
	padding: 0;
	list-style: none;
}

header ul li {
	float: left;
	position: relative;
}

header ul li ul {
	position: absolute;
	top: 66px;
	right: 0;
	width: 180px;
	display: none;
	z-index: 88888;
}

header ul li:hover ul {
	display: block;
}

header ul li ul li {
	width: 100%;
}

header ul li ul li a {
	padding: 10px;
	background: white;
	color: #444;
}

header ul li ul li a.logout {
	color: red;
}

header ul li ul li a:hover {
	background: #d5d6d6;
}

header ul li a {
	display: block;
	padding: 21px;
	font-size: 1.1em;
	text-decoration: none;
}

header ul li a:hover {
	background-color: skyblue;
	transition: 0.5s;
}

header .menu-toggle {
	display: none;
}

.page-wrapper {
	min-height: 100%;
	background: azure;
}

.page-wrapper a:hover {
	color: #006669;
}

/* POST SLIDER */
.post-slider {
	position: relative;
}

.post-slider .slider-title {
	text-align: center;
	/*margin: 30px auto;*/
	color: skyblue;
	background-color: none;
	padding: 10% auto;
}

.post-slider .next {
	position: absolute;
	top: 50%;
	right: 30px;
	font-size: 2em;
	color: #006669;
	cursor: pointer;
}

.post-slider .prev {
	position: absolute;
	top: 50%;
	left: 30px;
	font-size: 2em;
	color: #006669;
	cursor: pointer;
}

.post-slider .post-wrapper {
	width: 84%;
	height: 350px;
	margin: 0 auto;
	overflow: hidden;
	padding: 10px 0 10px 0;
}

.post-slider .post-wrapper .post {
	width: 300px;
	height: 330px;
	margin: 0 10px;
	display: inline-block;
	background: white;
	border-radius: 5px;
	box-shadow: 1rem 1rem 1rem -1rem #a0a0a033;
}

.post-slider .post-wrapper .post .post-info {
	height: 130px;
	padding: 0 5px;
}

.post-slider .post-wrapper .post .slider-image {
	width: 95%;
	height: 200px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}

/* CONTENT */

.content {
	width: 90%;
	margin: 30px auto 30px;
}

.content .main-content {
	width: 70%;
	float: left;
}

.content .main-content.single {
	background: white;
	padding: 20px 50px;
	font-size: 1.1em;
	border-radius: 5px;
}

.content .main-content.single .post-title {
	text-align: center;
	margin-bottom: 40px;
}

.content .main-content .post {
	width: 95%;
	height: 270px;
	margin: 20px auto;
	border-radius: 5px;
	background: white;
	position: relative;
}

.prev, .next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 24px;
  z-index: 1;
}

.content .popular .post {
	border-top: 1px solid #e0e0e0;
	margin-top: 10px;
	padding-top: 10px;
}

.content .popular .post img {
	height: 60px;
	width: 75px;
	float: left;
	margin-right: 10px;
}

.content .main-content .post .post-preview i {
	color: #757575;
	font-size: 11px;
	margin-right: 10px;
}

.content .main-content .post .preview-text {
	line-height: 1.5em;
	font-size: 1.0em;
	color: #444;
	margin-top: ;
}

.content .main-content .post .read-more {
	position: absolute;
	bottom: 10px;
	right: 10px;
	border: 1px solid #006669;
	background: transparent;
	border-radius: 0;
	color: #006669 !important;
}

.content .main-content .post .read-more:hover {
	background: #006669;
	color: white !important;
	transition: 0.25s;
}

.content .main-content .post .post-image {
	width: 40%;
	height: 100%;
	float: left;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
}

.content .main-content .post .post-preview {
	width: 60%;
	padding: 10px;
	float: right;
}

.content .main-content .recent-post-title {
	margin: 20px;
}

.content .sidebar {
	width: 30%;
	float: left;
}

.content .sidebar.single {
	padding: 0 10px;
}

.content .sidebar .section {
	background: white;
	padding: 20px;
	border-radius: 5px;
	margin-bottom: 20px;
}

.content .sidebar .section .section-title {
	margin: 10px 0p 10px 0;
}

.content .sidebar .section.search {
	margin-top: 80px;
}

.content .sidebar .section.topics ul {
	margin: 0;
	padding: 0;
	list-style: none;
	border-top: 1px solid #e0e0e0;
}

.content .sidebar .section.topics ul li a {
	display: block;
	padding: 15px 0 15px 0;
	border-bottom: 1px solid #e0e0e0;
	transition: all 0.3s;
}

.content .sidebar .section.topics ul li a:hover {
	padding-left: 10px;
	transition: all 0.3s;
}

/* ARTICLE */

.responsive-image {
  display: block;
  max-width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.share-buttons-container {
  display: flex;
  justify-content: left;
  margin-top: 20px;
  max-width: 400px; 
}

#author-label {
	font-weight: bold;
}

.author-label-home, .date-home{
  font-size: 12px;
  margin-right: 5px;
  font-family: "Lora", serif;
  color: grey;
}

#article-title {
	margin: 0px;
}

#article-date-time {
	font-size: 12px;
	font-family: arial;
	color: grey;
}

.share-buttons {
  display: flex;
}

/* MY POSTS */

.post-list {
  list-style-type: none;
  padding: 0;
}

.post-card {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  height: auto;
}

.post-media {
  width: 125px;
  height: 125px;
  object-fit: cover;
  margin-right: 20px;
}

.post-content {
  display: flex;
  flex-direction: column;
}

.post-content h3 {
  margin: 0 0 10px 0;
}

.post-content p {
  margin: 0 0 10px 0;
}

#read-more {
	padding: 5px;
	border:solid 1px grey;
	width: 120px;
	text-align: center;
	border-radius: 20px;
	background-color: gray;
}

/* POST DETAIL */

.post-detail-content {
	margin: 15px;
}

.post-detail-main-content {
	margin: 0px 100px 0px 100px;
}

/* AUTH PAGES */

.auth-content {
	width: 30%;
	margin: 50px auto;
	padding: 20px;
	background: white;
	border-radius: 5px;
}

.auth-content .form-title {
	text-align: center;
}

.auth-content form div {
	margin-bottom: 10px;
}

.auth-content form p {
	text-align: center;
}

.auth-content form p a {
	text-decoration: underline;
}

/* FOOTER */

.footer {
	background: pink;
	color: white;
	height: 400px;
	position: relative;
}

.footer .footer-content {
	height: 350px;
	display: flex;
}

.footer .footer-content .footer-section {
	flex: 1;
	padding: 25px;
}

.footer .footer-content h1,
.footer .footer-content h2 {
	color: white;
}

.footer .footer-content .about h1 span {
	color: #05f7ff;
}

.footer .footer-content .about .contact span {
	display: block;
	font-size: 1.1em;
	margin-bottom: 8px;
}

.footer .footer-content .about .socials a {
	border: 1px solid gray;
	width: 45px;
	height: 41px;
	padding-top: 5px;
	margin-right: 5px;
	text-align: center;
	display: inline-block;
	font-size: 1.3em;
	border-radius: 5px;
	transition: all 0.3s;
}

.footer .footer-content .about .socials a:hover {
	border: 1px solid white;
	color: white;
	transition: all 0.3s;
}

.footer .footer-content .links ul a {
	display: block;
	margin-bottom: 10px;
	font-size: 1.2em;
	transition: all 0.3s;
}

.footer .footer-content .links ul a:hover {
	color: white;
	margin-left: 15px;
	transition: all 0.3s;
}

.footer .footer-content .contact-form .contact-input {
	background: #272727;
	color: #bebdbd;
	margin-bottom: 10px;
	line-height: 1.5rem;
	padding: 0.9rem 1.4rem;
	border: none;
}

.footer .footer-content .contact-form .contact-input:focus {
	background: #1a1a1a;
}

.footer .footer-content .contact-form .contact-btn {
	float: right;
	font-size: 1.1em;
	font-family: "Lora", serif;
}

.footer .footer-bottom {
	background: #343a40;
	color: #686868;
	height: 50px;
	width: 100%;
	text-align: center;
	position: absolute;
	bottom: 0;
	left: 0;
	padding-top: 20px;
}

.footer-bottom {
	background: #343a40;
	color: #686868;
	height: 50px;
	width: 100%;
	text-align: center;
	padding-top: 10px;
}

/* MEDIA QUERIES */

@media only screen and (max-width: 934px) {
	.content {
		width: 100%;
	}

	.auth-content {
		width: 50%;
	}

	.content .main-content .post {
		width: 100%;
	}

	.content .main-content {
		width: 100%;
		padding: 10px;
	}

	.content .sidebar {
		width: 100%;
		padding: 10px;
	}

	.content .sidebar .section.search {
		margin-top: 30px;
	}

	.footer {
		height: auto;
	}

	.footer .footer-content {
		height: auto;
		flex-direction: column;
	}

	.footer .footer-content .contact-form {
		padding-bottom: 90px;
	}

	.footer .footer-content .contact-form .contact-btn {
		width: 100%;
	}
}

@media only screen and (max-width: 770px) {
	header {
		position: relative;
	}

	.auth-content {
		width: 60%;
	}

	.main-content-wrapper {
		padding: 10px;
	}

	header ul {
		width: 100%;
		background: #0e94a0;
		max-height: 0;
		overflow: hidden;
	}

	.showing {
		max-height: 100em;
	}

	header ul li {
		width: 100%;
	}

	header ul li ul {
		position: static;
		display: block;
		width: 100%;
	}

	header ul li ul li a {
		padding: 10px;
		background: #0e94a0;
		color: white;
		padding-left: 50px;
	}

	header ul li ul li a:hover {
		background: #006669;
	}

	header ul li ul li a.logout {
		color: red;
	}

	header .menu-toggle {
		display: block;
		position: absolute;
		right: 20px;
		top: 20px;
		font-size: 1.9em;
	}

	header .logo {
		margin-left: 0.5em;
	}
}

@media only screen and (max-width: 600px) {
	.content .main-content .post {
		height: auto;
	}

	.auth-content {
		width: 90%;
	}

	.content .main-content.single {
		padding: 10px 20px;
	}

	.content .main-content .post .post-image {
		width: 100%;
	}

	.content .main-content .post .post-preview {
		width: 100%;
	}

	.content .main-content .post .read-more {
		position: static;
		display: block;
		width: 100%;
		text-align: center;
	}

	.post-slider .next {
		right: 10px;
	}

	.post-slider .prev {
		left: 10px;
	}
}

.div {
	padding: 1.5rem 0;
}

div.content.clearfix {
	margin: 30px 99px 0 99px;
}

@media screen and (max-width: 680px) {
	body > div.page-wrapper > div.content.clearfix {
		margin: 30px 0 0 0;
	}
	body > header{
		width: 100%;
	}

	div.content.clearfix {
	margin: 30px 99px 0 0px;
	}

	.post-detail-main-content {
		margin: 0px;
	}
	
}
